/* Container */

/* App.css */

/* Style for the whole app */
.App {
  font-family: Arial, sans-serif;
  text-align: center;
}

/* Style for the navbar */
.navbar {
  background-color: #f0f0f0;
  padding: 10px;
}

.navbar ul {
  list-style-type: none;
  padding: 0;
}

.navbar ul li {
  display: inline;
  margin-right: 20px;
}

/* Style for links in navbar */
.navbar ul li a {
  text-decoration: none;
}

/* Style for search form */
.form-inline {
  display: inline-block;
}

/* Style for search input */
.form-control {
  width: 200px;
  margin-right: 10px;
}

/* Style for search button */
.btn {
  background-color: #4CAF50; /* Green */
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}

/* Change the color of the search button on hover */
.btn:hover {
  background-color: #45a049;
}

/* Style for the main content */
.content {
  margin-top: 20px;
}

/* Style for headings */
h2 {
  color: #191970;
}
